.post{padding:2em 0;border-bottom:1px solid #d2d2d2}.post .post-header{padding:0.8em;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;color:#4e4187}.post .post-header .post-header-text{margin-left:1em;margin-right:1em;font-weight:normal}.post .post-body{padding:1em;border-radius:0 1em 1em;background-color:#4e4187;color:white}.post .post-body .post-date{font-size:0.8em}.post .post-statistics{font-size:0.8em;margin:1.4em 0}.post .post-comments{font-size:0.8em}

